import React, {Component} from "react";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {Link} from "react-router-dom";
import Select from "react-select";
import {getUsers} from "../actions/userActions";
import {getBreakdownTypes} from "../actions/breakdownTypeActions";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"
import ru from 'date-fns/locale/ru';
import {Button, Col, Form, Row} from "react-bootstrap";
import history from "../global/history"

class ReportSettings extends Component {

  getStartDate = () => {
    const date = new Date();
    date.setMonth(date.getMonth() - 1);
    return date;
  }

  state = {
    start: this.getStartDate(),
    end: new Date(),
    breakdownType: [],
    user: null,
    status: "",
    isOpenStart: false,
    isOpenEnd: false
  }

  componentDidMount() {
    const {getBreakdownTypes, getUsers, users, breakdownType} = this.props;
    if (users.length === 0) {
      getUsers("USER");
    }
    if (breakdownType.length === 0) {
      getBreakdownTypes();
    }
  }

  handleChangeBreakdown = (breakdownType) => {
    this.setState({breakdownType});
  }

  handleChangeUsers = (user) => {
    this.setState({user});
  }

  onChange = (e) => {
    this.setState({[e.target.name]: e.target.value});
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({errors: nextProps.errors});
    }
  }

  breakdownToOptions = (breakdowns) => {
    let result = breakdowns.map(breakdown => ({value: breakdown.id, label: breakdown.type}));
    result.unshift({value: 0, label: " "})
    return result;
  }

  userToOptions = (users) => {
    return users
      .filter(user => user.role === "USER")
      .map(user => ({value: user.id, label: user.fullName}));
  }

  handleChangeStartDate = (date) => {
    this.setState({
      start: date
    });
    this.toggleCalendarStart()
  }

  handleChangeEndDate = (date) => {
    this.setState({
      end: date
    });
    this.toggleCalendarEnd()
  }


  toggleCalendarStart = (e) => {
    e && e.preventDefault()
    this.setState({isOpenStart: !this.state.isOpenStart})
  }

  toggleCalendarEnd = (e) => {
    e && e.preventDefault()
    this.setState({isOpenEnd: !this.state.isOpenEnd})
  }

  formatDate = (date) => {
    var monthNames = [
      "", "", "",
      "", "", "", "",
      "", "", "",
      "", ""
    ];

    var day = date.getDate();
    var monthIndex = date.getMonth();
    var year = date.getFullYear();

    return day + ' ' + monthNames[monthIndex] + ' ' + year;
  }

  convertOptionToUsers = (userChooseList) => {
    const {users} = this.props;
    let result = []
    userChooseList.forEach(choose => {
      const chooseUser = users.find(user => user.id === choose.value)

      if (chooseUser) {
        return result.push(chooseUser.id)
      }
    })
    return result;
  }

  convertOptionToBreakdown = (breakdownChooseList) => {
    const {breakdownType} = this.props;
    const result = []
    breakdownChooseList.forEach(choose => {

      if (choose.value === 0) {
        result.push(choose.value)
        return;
      }

      const chooseBreakdown = breakdownType.find(type => type.id === choose.value)

      if (chooseBreakdown) {
        result.push(chooseBreakdown.id)
      }
    })

    return result;
  }

  onSubmit = (e) => {
    e.preventDefault();

    const reportSettings = {
      breakdownType: this.convertOptionToBreakdown(this.state.breakdownType),
      status: this.state.status,
      start: this.state.start,
      end: this.state.end,
      users: this.convertOptionToUsers(this.state.user),
    };

    this.context.router.history.push("/report/" + btoa(JSON.stringify(reportSettings)));
    // history.push("/antenna12/#/report/" ))

  }

  render() {
    const {breakdownType, users} = this.props;
    return (
      <div className="add-PBI">
        <div className="container">
          <div className="row">
            <div className="col-md-8 m-auto">
              <Link to={"/dashboard"} className="btn btn-light" style={{marginRight: "10px"}}>
                  
              </Link>
              <Link to={"/adresses"} className="btn btn-light">
                 
              </Link>
              <h4 className="display-4 text-center"> </h4>
              <Form onSubmit={this.onSubmit}>

                <Form.Group as={Row} controlId="formHorizontalEmail">
                <Form.Label column sm={2}>
                  
                </Form.Label>
                <Col sm={10}>
                  <Button block variant="outline-secondary"
                          onClick={this.toggleCalendarStart}
                  >{this.formatDate(this.state.start)}</Button>
                </Col>
              </Form.Group>

                {
                  this.state.isOpenStart && (
                    <DatePicker
                      selected={this.state.start}
                      onChange={this.handleChangeStartDate}
                      dateFormat="dd-MM-yyyy"
                      locale={ru}
                      withPortal
                      inline
                      onClickOutside={this.toggleCalendarStart}
                    />)
                }

                <Form.Group as={Row} controlId="formHorizontalEmail">
                  <Form.Label column sm={2}>
                    
                  </Form.Label>
                  <Col sm={10}>
                    <Button block variant="outline-secondary"
                            onClick={this.toggleCalendarEnd}
                    >{this.formatDate(this.state.end)}</Button>
                  </Col>
                </Form.Group>

                {
                  this.state.isOpenEnd && (
                    <DatePicker
                      selected={this.state.end}
                      onChange={this.handleChangeEndDate}
                      dateFormat="dd-MM-yyyy"
                      locale={ru}
                      withPortal
                      inline
                      onClickOutside={this.toggleCalendarEnd}
                    />)
                }

                <div className="form-group">
                  <Select
                    placeholder={" "}
                    value={this.state.breakdownType}
                    onChange={this.handleChangeBreakdown}
                    options={this.breakdownToOptions(breakdownType)}
                    isMulti
                  />
                </div>

                <div className="form-group">
                  <Select
                    placeholder={" "}
                    value={this.state.users}
                    onChange={this.handleChangeUsers}
                    options={this.userToOptions(users)}
                    isMulti
                  />
                </div>

                <div className="form-group">
                  <select
                    className="form-control form-control-sm"
                    name="status"
                    value={this.state.status}
                    onChange={this.onChange}
                  >
                    <option value=""></option>
                    <option value="ALL"></option>
                    <option value="TO_DO"> </option>
                    <option value="IN_PROGRESS"> </option>
                    <option value="DONE"></option>

                  </select>
                </div>

                <input
                  type="submit" value={""}
                  className="btn btn-primary btn-block mt-4"
                />
              </Form>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

ReportSettings.propTypes = {
  errors: PropTypes.object.isRequired,
  breakdownType: PropTypes.array.isRequired,
  getBreakdownTypes: PropTypes.func.isRequired,
  users: PropTypes.array.isRequired,
  getUsers: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  errors: state.errors,
  breakdownType: state.breakdownType,
  users: state.users
});

export default connect(
  mapStateToProps,
  {getUsers, getBreakdownTypes}
)(ReportSettings);
